const lead = require("lead");
const build = require("build");
const school = require("school");
const role = require("role");
const skill = require("skill");
const prop = require("prop");

let api = {
    getEvent() {
        return lead.event;
    },
    addEvent(obj) {
        lead.event.push(obj);
    },
    removeEvent(obj) {
        lead.event.forEach((item, index) => {
            if (item == obj) {
                lead.event.splice(index, 1);
            }
        });
    },
    clearEvent() {
        lead.event = [];
        lead.event.length = 0;
    },
    getMaster(num) {
        return role.queryroleID(school.getSchool(num).army[0]);
    },
    getAll() {
        return lead;
    },
    getName() {
        return lead.name;
    },
    getMoney() {
        return lead.Money;
    },
    getRepute() {
        return lead.repute;
    },
    getSchool() {
        return lead.school;
    },
    getOptionAll() {
        let n = arguments[0];
        if (!n) return lead.option;
        return [].filter.call(lead.option, (a, i) => {
            return i < n
        })
    },
    getAbilityAll() {
        return lead.ability;
    },
    getSkillAll() {
        return lead.skill;
    },
    getEquipmentAll() {
        return lead.equipment;
    },
    getPropAll() {
        return lead.prop;
    },
    getAbility(name) {
        switch (name) {
            case "Money":
                return lead[name];
                break;
            case "repute":
                return lead[name];
                break;
            default:
                return lead.ability[name];
                break;
        }

    },
    setAbility(name, num, obj) {
        let project = obj || lead;
        switch (name) {
            case "Money":
                lead[name] += ~~num;
                lead["Money"] = Math.min(lead["Money"], 99999999)
                break;
            case "repute":
                lead[name] += ~~num;
                lead["repute"] = Math.min(lead["repute"], 99999999)
                break;
            case "HP":
                let maxhp = project["ability"]["MAXHP"];
                project.ability[name] = Math.min(maxhp, ~~project.ability[name] + num);
                break;
            case "MP":
                let maxmp = project.ability["MAXMP"];
                project.ability[name] = Math.min(maxmp, ~~project.ability[name] + num);
                break;
            default:
                project.ability[name] += ~~num;
                break;
        }
    },
    setOption(obj) {
        lead.option.push(obj)
    },
    setSkill(name, man, level, Exp) {
        let item = skill.querySkill(name);
        if (typeof man == "string") {
            man = role.getRoleData(man);
        }
        if (item) {
            item.Exp = Exp || 0;
            item.level = level || 1;
            if (man) {
                man.skill.push(item);
            } else {
                lead.skill.push(item);
            }
        }
    },
    getPropData() {
        return prop.getData();
    },
    setPropData(data) {
        prop.setData(data);
    },
    removeProp(obj, type) {
        let arr = lead.prop;
        if (type) {
            arr.forEach((item, index) => {
                if (item["id"] == obj["id"]) {
                    arr.splice(index, 1);
                }
            })
        } else {
            arr.forEach((item, index) => {
                if (item["name"] == obj["name"]) {
                    arr.splice(index, 1);
                }
            })
        }

    },
    setProp() {
        let arr = lead.prop;
        let bool = false;
        let obj = Object.assign(arguments[0]);
        arr.forEach(item => {
            if (item["name"] == obj["name"] && item["type"] != 4 && item["type"] != 6) {
                item["num"]++;
                bool = true;
            }
        });
        if (!bool) {
            prop.add(obj);
            obj.num = 1;
            obj.id = prop.getLen() + 1;
            arr.push(obj);
        }
    },
    setEquipment(obj, man) {
        let project = man || lead;
        let owner = obj["owner"];
        let ownerData = null;
        if (owner) {
            ownerData = role.getRoleData(owner);
            ownerData["equipment"].forEach((item, index) => {
                if (item["id"] == obj["id"]) {
                    checkAttr(item).forEach(n => {
                        ownerData["ability"][n["key"]] -= n["num"];
                    });
                    ownerData["equipment"].splice(index, 1);
                }
            })
        }
        project.equipment.forEach((item, index) => {
            if (item["type"] == obj["type"]) {
                item["owner"] = "";
                project.equipment.splice(index, 1);
            }
        })
        obj["owner"] = project["name"];
        project.equipment.push(obj)
    },
    setName(txt) {
        lead.name = txt;
    },
    setSchool(txt) {
        lead.school = txt;
    },
    setMyBuild(name) {
        let _obj = build.queryBuild(name);
        _obj.level = 1;
        lead.build.push(_obj)
    },
    upMyBuild(name) {
        let _obj = build.queryBuild(name);
        if (_obj.level < _obj.maxlevel) _obj.level += 1;
    },
    getMyBuild(name) {
        if (!name) return lead.build;
        else return build.queryBuild(name);
    },
    getMyHeadpic() {
        return lead.headpic;
    }
}

function checkAttr(obj) {
    let attr = ["MAXHP", "MAXMP", "LER", "DEF", "ATN", "SPD", "WIL"];
    let arr = [];
    for (const key in obj) {
        if (obj.hasOwnProperty(key)) {
            if (attr.indexOf(key) > -1) {
                arr.push({
                    key: key,
                    num: obj[key]
                })
            }
        }
    }
    return arr;
}
module.exports = api;