const com = require("./common/common");
const system = require("./common/system");
const parms = require("./parameter/setparms");
const school = require("./parameter/school");

cc.Class({
    extends: cc.Component,

    properties: {
        around: {
            default: null,
            type: cc.Label
        },
        moeny: {
            default: null,
            type: cc.Label
        },
        repute: {
            default: null,
            type: cc.Label
        },
        instructions_build: {
            default: null,
            type: cc.Node
        },
        instructions_army: {
            default: null,
            type: cc.Node
        },
        instructions_prop: {
            default: null,
            type: cc.Node
        },
        instructions_diplomacy: {
            default: null,
            type: cc.Node
        },
        instructions_next: {
            default: null,
            type: cc.Node
        },
        msg: {
            default: null,
            type: cc.Node
        },
        num: {
            default: null,
            type: cc.Label
        },
        shool: {
            default: null,
            type: cc.Label
        },
        standing: {
            default: null,
            type: cc.Label
        },
        pic: {
            default: null,
            type: cc.Sprite
        }
    },

    onLoad() {
        //初始主界面
        this.initAround();
    },
    start() {
        //回合准备
        this.beginAction();
    },
    initAround() {
        let stu_box = this.node.getChildByName("stu_box");
        let sys_box = this.node.getChildByName("sys_box");

        this.showAround() //显示回合
        this.showMoney(); //显示金钱
        this.showRepute() //显示声望
        this.checkMyBuild(); //显示建筑

        let eventbox = this.node.getChildByName("tanhaobox");
        let build_system = this.node.getChildByName("build_system");
        let role_system = this.node.getChildByName("role_system");

        /* 开启建造界面 */
        this.instructions_build.on(cc.Node.EventType.TOUCH_END, () => {
            cc.director.loadScene("build_system");
        });
        /* 开启人物界面 */
        this.instructions_army.on(cc.Node.EventType.TOUCH_END, () => {
            cc.director.loadScene("role_system");
        });
        /* 开启道具界面 */
        this.instructions_prop.on(cc.Node.EventType.TOUCH_END, () => {
            cc.director.loadScene("prop_system");
        });
        /* 开启外交界面 */
        this.instructions_diplomacy.on(cc.Node.EventType.TOUCH_END, () => {
            cc.director.loadScene("diplomacy_system");
        });
        /* 结束回合 */
        this.instructions_next.on(cc.Node.EventType.TOUCH_END, () => {
            eventbox.active = false;
            stu_box.runAction(cc.moveTo(.4, 0, 350));
            sys_box.runAction(cc.sequence(cc.moveTo(.4, 0, -370), cc.callFunc(() => {
                com.changeActive(); //战略界面展示
                com.nextPlayer(); //下一回合
                this.beginAction(); //战略界面执行事件 
                this.clearEvent(); //清除部分特别事件
            })));
        })
    },
    beginAction() {
        let eventbox = this.node.getChildByName("tanhaobox");
        let url = "resources/img/role/";
        let player = com.getplayer(); //获取玩家
        let around = com.getAround(); //获取回合
        let playerData = com.getplayerData(); //获取玩家数据
        let ms = school.getSchool(player); //获取门派数据
        let mt = parms.getMaster(player); //获取掌门数据

        this.pic.spriteFrame = com.loadFrame(url + mt.headpic);
        this.shool.string = `${ms.name}`;
        this.num.string = `成员：${ms.army.length}`;
        this.standing.string = `掌门：${mt.name}`;
        let stu_box = this.node.getChildByName("stu_box");
        let sys_box = this.node.getChildByName("sys_box");
        let my_status = this.node.getChildByName("my_status");
        my_status.active = false;
        //如果未加载回合战略界面
        if (!com.getActive()) {
            my_status.active = true; //显示回合战略
            let msg_arr = [];
            msg_arr.push(ms.msg[~~(Math.random() * ms.msg.length)]);
            //如果是玩家回合
            if (player == 0) {
                //判断上回合获取收益 市场等级固定值+100以内随机金钱
                let mybuild = parms.getMyBuild();
                let market = this.queryHas(mybuild, "市场");
                if (market["level"] > 0) {
                    let money = ~~market["num"][market["level"] - 1] + ~~(100 * Math.random());
                    msg_arr.push(`上回合门派收益${money}两`);
                    parms.setAbility("Money", money);
                }
                this.showAround() //显示回合
                this.showMoney(); //显示金钱
                this.showRepute() //显示声望
            }
            if (player != 0) {
                msg_arr.push(mt.name + '正思考着什么...');
            }
            let index = 0;
            this.msg.getComponent(cc.Label).string = msg_arr[index];
            this.msg.x = 960;
            let seq = cc.sequence(cc.repeat(cc.sequence(
                cc.moveTo(.8, 0, 0),
                cc.delayTime(2),
                cc.moveTo(.8, -960, 0),
                cc.callFunc(() => {
                    this.msg.x = 960;
                    index++;
                    this.msg.getComponent(cc.Label).string = msg_arr[index];
                })
            ), msg_arr.length), cc.callFunc(() => {
                if (player == 0) {
                    my_status.active = false;
                    stu_box.runAction(cc.moveTo(.4, 0, 290));
                    sys_box.runAction(cc.moveTo(.4, 0, -270));
                    com.changeActive();
                    this.initEvent();
                } else {
                    com.nextPlayer();
                    this.beginAction();
                }
                index = 0;
            }));
            this.msg.runAction(seq);
        }
        //如果加载完回合战略界面
        else {
            this.checkEvent();
            stu_box.runAction(cc.moveTo(.4, 0, 290));
            sys_box.runAction(cc.moveTo(.4, 0, -270));
        }
    },
    checkMyBuild() {
        let landbox = this.node.getChildByName("landbox").getChildren();
        let mybuild = parms.getMyBuild();
        for (let i = 0; i < landbox.length; i++) {
            let item = landbox[i]._name;
            for (let j = 0; j < mybuild.length; j++) {
                const element = mybuild[j];
                if (element.src == item && !landbox[i].active) {
                    landbox[i].active = true;
                }
            }
        }
        this.showMoney();
    },
    showMoney() {
        this.moeny.string = parms.getMoney();
    },
    showAround() {
        this.around.string = '第' + com.getDate(system.around) + "回合";
    },
    showRepute() {
        this.repute.string = parms.getRepute();
    },
    initEvent() {
        let mybuild = parms.getMyBuild();
        let eventbox = this.node.getChildByName("tanhaobox");
        eventbox.active = true;

        //判断商人事件 shop_event
        //市场每升一级 商人概率增加 15%
        let market = this.queryHas(mybuild, "市场");
        if (market) {
            let gl = ~~(Math.random() * 100) <= ~~market["level"] * 15;
            console.log("shop_event：" + gl);
            if (gl) {
                parms.addEvent("shop_event")
            }
        }
        this.checkEvent();
    },
    checkEvent() {
        let eventbox = this.node.getChildByName("tanhaobox");
        eventbox.getChildren().forEach(item => {
            item.active = false;
        })
        parms.getEvent().forEach(item => {
            let event = eventbox.getChildByName(item);
            event.active = true;
        })
    },
    clearEvent() {
        parms.removeEvent("shop_event")
    },
    queryHas(arr, name) {
        for (let i = 0; i < arr.length; i++) {
            if (arr[i]["name"] == name) {
                return arr[i];
            }
        }
        return false;
    }
    // update(dt) {

    // }
});