const system = require("system");
const school = require("../parameter/school");
let api = {
    getDate() {
        let section = ~~arguments[0];
        let chnNumChar = ["零", "一", "二", "三", "四", "五", "六", "七", "八", "九"];
        let chnUnitSection = ["", "万", "亿", "万亿", "亿亿"];
        let chnUnitChar = ["", "十", "百", "千"];
        let strIns = '';
        let chnStr = '';
        let unitPos = 0;
        let zero = true;
        while (section > 0) {
            let v = section % 10;
            if (v === 0) {
                if (!zero) {
                    zero = true;
                    chnStr = chnNumChar[v] + chnStr;
                }
            } else {
                zero = false;
                strIns = chnNumChar[v];
                strIns += chnUnitChar[unitPos];
                chnStr = strIns + chnStr;
            }
            unitPos++;
            section = Math.floor(section / 10);
        }
        return chnStr;
    },
    loadFrame() {
        return new cc.SpriteFrame(cc.url.raw(arguments[0]));
    },
    getUnit() {
        return system.unit;
    },
    changeActive() {
        system.active = !system.active;
    },
    getActive() {
        return system.active;
    },
    getplayerData() {
        return school.getSchool(system.player);
    },
    getAround() {
        return system.around;
    },
    getplayer() {
        return system.player;
    },
    nextPlayer() {
        system.player++;
        if (system.player > school.get().length - 1) {
            system.player = 0;
            system.around++;
        }
    },
    getJSON(url) {
        const promise = new Promise(function(resolve, reject) {
            const handler = function() {
                if (this.readyState !== 4) {
                    return;
                }
                if (this.status === 200) {
                    resolve(this.response);
                } else {
                    reject(new Error(this.statusText));
                }
            };
            const client = new XMLHttpRequest();
            client.open("GET", url);
            client.onreadystatechange = handler;
            client.responseType = "json";
            client.setRequestHeader("Accept", "application/json");
            client.send();

        });
        return promise;
    }
}

module.exports = api;