let prop = [];
let newprop = [];
module.exports = {
    setData(data) {
        prop = data;
    },
    getData() {
        return prop;
    },
    getLen() {
        return prop.length + newprop.length;
    },
    add() {
        newprop.push(arguments[0])
    },
    getProp() {
        let index = arguments[0];
        if (!index) return prop;
        return prop[index];
    },
    queryProp(name) {
        for (let i = 0; i < prop.length; i++) {
            if (prop[i]["name"] === name) return Object.assign(prop[i]);
        }
    }
}