const system = require("./common/system");
const parms = require("./parameter/setparms");
const school = require("./parameter/school");
const role = require("./parameter/role");
const option = require("./parameter/option");
cc.Class({
    extends: cc.Component,

    properties: {
        input_box: {
            default: null,
            type: cc.Node
        },
        option_box: {
            default: null,
            type: cc.Node
        },
        next_ico: {
            default: null,
            type: cc.Node
        },
        bg_pic: {
            default: null,
            type: cc.Sprite
        },
        head_pic: {
            default: null,
            type: cc.Sprite
        },
        talk: {
            default: null,
            type: cc.RichText
        },
        role_name: {
            default: null,
            type: cc.Label
        },
        list: [],
        scenes: null,
        num: 0,
        max: null,
        pause: false,
    },

    onLoad() {
        this.bindInit();
        cc.director.setDisplayStats(false);
    },
    bindInit() {
        this.option_box.active = false;
        this.input_box.active = false;
        cc.loader.loadRes("data/talk", cc.RawAsset, (err, res) => {
            this.list = res.data[system.progress].list;
            this.scenes = res.data[system.progress].scenes;
            this.max = this.list.length;
            this.bg_pic.spriteFrame = this.loadFrame(res.data[system.progress].bg_pic);
            this.nextPlot();
        })
    },
    fadeAction(callback) {
        let beginAction = cc.sequence(cc.fadeOut(1), cc.callFunc(() => { callback && callback() }), cc.fadeIn(.6))
        this.node.runAction(beginAction);
    },
    start() {
        this.node.on('touchend', () => {
            this.nextPlot();
        });
        for (let i = 0; i < this.option_box.getChildren().length; i++) {
            let obj = this.option_box.children[i];
            ~((i) => {
                obj.on(cc.Node.EventType.TOUCH_START, () => {
                    obj.scale = 1.2;
                }, this)
                obj.on(cc.Node.EventType.TOUCH_END, () => {
                    obj.scale = 1;
                    this.bindOption(i, obj.children[0].getComponent(cc.Label).string);
                }, this)
            })(i);
        }
    },
    bindOption(i, n) {
        parms.setOption({ id: i, name: n });
        // if (parms.getOptionAll().length == 6) {
        //     let _op_id = parms.getOptionAll()[5].id;
        //     if (_op_id == 1) {
        //         this.num = this.max;
        //     }
        // }
        if (n == "不需要") {
            this.num = this.max;
        }
        this.pause = false;
        this.num++;
        this.next_ico.active = true;
        this.option_box.active = false;
        this.nextPlot();
    },
    showOption(option) {
        var _this = this;
        this.option_box.active = true;
        this.next_ico.active = false;
        for (let i = 0; i < this.option_box.getChildren().length; i++) {
            let obj = this.option_box.children[i];
            obj.scale = 1;
            obj.children[0].getComponent(cc.Label).string = option[i];
            if (option[i]) {
                obj.active = true;
            } else {
                obj.active = false;
            }
        }
    },
    showInput(relation, callback) {
        this.input_box.active = true;
        this.next_ico.active = false;
        let editbox = this.input_box.getChildren()[0].getChildren()[0];
        let label = this.input_box.getChildren()[1];
        label.active = false;
        switch (relation) {
            case "name":
                editbox.getComponent(cc.EditBox).placeholder = "姓甚名谁";
                break;
            case "school":
                editbox.getComponent(cc.EditBox).placeholder = "何门何派";
                break;
            default:
                break;
        }
        editbox.on('editing-did-ended', e => {
            let txt = editbox.getComponent(cc.EditBox).string;
            if (!txt) return false;
            switch (relation) {
                case "name":
                    parms.setName(txt)
                    break;
                case "school":
                    let str = txt.substr(txt.length - 1, txt.length);
                    if (str != "派" && str != "门" && str != "寨" && str != "教" && str != "帮") txt += "派";
                    if (school.querySchool(txt)) {
                        label.active = true;
                        label.getComponent(cc.Label).string = "你好像不是这一派的一份子，请重新填写";
                        return false;
                    }
                    label.active = false;
                    parms.setSchool(txt)
                    break;
                default:
                    break;
            }
            editbox.getComponent(cc.EditBox).string = "";
            editbox.off("editing-did-ended");
            callback && callback();
        })
    },
    nextPlot() {
        if (this.max > this.num) {
            if (!this.pause) {
                this.head_pic.spriteFrame = this.loadFrame(this.list[this.num].role_pic);
                if (this.list[this.num].status == 0) {
                    this.role_name.string = (parms.getName() || this.list[this.num].role_name) + ":";
                } else {
                    this.role_name.string = this.list[this.num].role_name + ":";
                }
                switch (this.list[this.num].type) {
                    case "0":
                        this.talk.string = this.list[this.num].content;
                        this.num++;
                        break;
                    case "1":
                        this.talk.string = this.list[this.num].content;
                        this.pause = true;
                        this.showOption(this.list[this.num].option);
                        break;
                    case "2":
                        this.talk.string = `${this.list[this.num].content[0]}${parms.getOptionAll()[parms.getOptionAll().length-1]["name"]}${this.list[this.num].content[1]}${this.list[this.num].option[(parms.getOptionAll())[parms.getOptionAll().length-1]["id"]]}${this.list[this.num].content[2]}`;
                        this.num++;
                        break;
                    case "3":
                        this.talk.string = this.list[this.num].content;
                        this.pause = true;
                        this.showInput(this.list[this.num].relation, () => {
                            this.pause = false;
                            this.input_box.active = false;
                            this.num++;
                            this.next_ico.active = true;
                            //this.nextPlot()
                        });
                        break;
                    case "4":
                        let txt = "";
                        switch (this.list[this.num].relation) {
                            case "name":
                                txt = parms.getName();
                                break;
                            case "school":
                                txt = parms.getSchool();
                                break;
                            default:
                                break;
                        }
                        this.talk.string = `${this.list[this.num].content[0]}${txt}${this.list[this.num].content[1]}`;
                        this.num++;
                        break;
                    default:
                        break;
                }
            }
        } else {
            switch (system.progress) {
                case 0:
                    option.Opening();
                    this.initSchool();
                    this.pause = true;
                    system.progress = 1;
                    this.fadeAction(() => {
                        this.bindInit();
                        this.pause = false;
                    })
                    break;
                case 1:
                    this.pause = true;
                    system.progress = 2;
                    this.fadeAction(() => {
                        this.bindInit();
                        this.pause = false;
                    });
                    break;
                case 2:
                    system.progress = 3;
                    cc.director.loadScene("sc_02");
                    break;
                default:
                    break;
            }
            this.num = 0;
        }
    },
    initSchool() {
        role.setRole(0).name = parms.getAll().name;
        role.setRole(0).headpic = parms.getAll().headpic;
        role.setRole(0).ability = parms.getAll().ability;
        role.setRole(0).type = parms.getAll().type;
        role.setRole(0).equipment = parms.getAll().equipment;
        role.setRole(0).skill = parms.getAll().skill;
        school.get()[0].name = parms.getAll().school;
    },
    loadFrame() {
        return new cc.SpriteFrame(cc.url.raw(arguments[0]));
    },



    // update (dt) {},
});