const com = require("./common/common");
const parms = require("./parameter/setparms");
const school = require("./parameter/school");
const role = require("./parameter/role");

const RES_URL = "resources/img/role/head/";
const RES_URL2 = "resources/img/icon/";
const RES_URL3 = "resources/img/prop/";
cc.Class({
    extends: cc.Component,

    properties: {
        head: {
            default: null,
            type: cc.Sprite
        },
        graphics: {
            default: null,
            type: cc.Graphics
        },
        graphics2: {
            default: null,
            type: cc.Graphics
        },
        attrRich: {
            default: null,
            type: cc.Node
        },
        attrMore: {
            default: null,
            type: cc.Node
        },
        skillbox: {
            default: null,
            type: cc.Node
        },
        skillperfab: {
            default: null,
            type: cc.Prefab
        },
        wuqi: {
            default: null,
            type: cc.Node
        },
        fangju: {
            default: null,
            type: cc.Node
        },
        xiedai: {
            default: null,
            type: cc.Node
        }
    },

    onLoad() {
        this.node.on("postInfo", res => {
            this.data = res.detail.data;
            this.showContent();
        })
    },
    start() {

    },
    showContent() {
        console.log(this.data);
        let sex = this.data["sex"]; //获取性别
        this.head.spriteFrame = com.loadFrame(`${RES_URL}${this.data["headpic"].split(".png")[0]}_200x200.png`); //显示图片
        this.setUi(sex); //显示性别图片
        this.drawAttr(); //显示5围
        this.getMoreAttr(); //显示其他属性
        this.getSkill(); //显示技能
        this.getEquipment(); //显示装备
    },
    getEquipment() {
        let equipment = this.data["equipment"];
        let pic = null;
        this.wuqi.getChildByName("kong").active = true;
        this.fangju.getChildByName("kong").active = true;
        this.xiedai.getChildByName("kong").active = true;
        this.wuqi.getChildByName("box").active = false;
        this.fangju.getChildByName("box").active = false;
        this.xiedai.getChildByName("box").active = false;
        equipment.forEach(item => {
            switch (~~item["type"]) {
                case 4:
                    this.wuqi.getChildByName("kong").active = false;
                    pic = this.wuqi.getChildByName("box");
                    pic.active = true;
                    pic.getChildByName("item").getComponent(cc.Sprite).spriteFrame = com.loadFrame(`${RES_URL3}${item["img"]}`);
                    break;
                case 6:
                    this.fangju.getChildByName("kong").active = false;
                    pic = this.fangju.getChildByName("box");
                    pic.active = true;
                    pic.getChildByName("item").getComponent(cc.Sprite).spriteFrame = com.loadFrame(`${RES_URL3}${item["img"]}`);
                    break;
                case 7:
                    this.xiedai.getChildByName("kong").active = false;
                    pic = this.xiedai.getChildByName("box");
                    pic.active = true;
                    pic.getChildByName("item").getComponent(cc.Sprite).spriteFrame = com.loadFrame(`${RES_URL3}${item["img"]}`);
                    break;
                default:
                    break;
            }
        })
    },
    setUi() {
        let man0 = this.node.getChildByName("man0");
        let man1 = this.node.getChildByName("man1");
        if (arguments[0] == 1) {
            man1.active = true;
            man0.active = false;
        } else {
            man1.active = false;
            man0.active = true;
        }
    },
    getMoreAttr() {
        let HP = this.data["ability"]["HP"];
        let MAXHP = this.data["ability"]["MAXHP"];
        let m_hp = this.attrMore.getChildByName("HP");
        let n_hp = m_hp.getChildByName("num");
        n_hp.width = m_hp.width * HP / MAXHP - 2;

        let MP = this.data["ability"]["MP"];
        let MAXMP = this.data["ability"]["MAXMP"];
        let m_mp = this.attrMore.getChildByName("MP");
        let n_mp = m_mp.getChildByName("num");
        n_mp.width = m_mp.width * MP / MAXMP - 2;

        let EXP = this.data["ability"]["EXP"];
        let MAXEXP = this.data["ability"]["NEXT"];
        let m_exp = this.attrMore.getChildByName("EXP");
        let n_exp = m_exp.getChildByName("num");
        n_exp.width = m_exp.width * EXP / MAXEXP - 2;



        let Level = this.data["ability"]["Level"];
        let MAXLevel = this.data["ability"]["MAXLevel"];
        let n_level = this.attrMore.getChildByName("Level").getComponent(cc.RichText);
        n_level.string = `<b><color=#AF1919>${com.getDate(Level)}</color></b><br/><color=#666666><size=12>潜能${MAXLevel}</size></color>`;

        this.attrMore.getChildByName("HP_STR2").getComponent(cc.Label).string = `${HP}/${MAXHP}`;
        this.attrMore.getChildByName("MP_STR2").getComponent(cc.Label).string = `${MP}/${MAXMP}`;
        this.attrMore.getChildByName("EXP_STR2").getComponent(cc.Label).string = `${EXP}/${MAXEXP}`;

        if (this.data["study"]) {
            let study_name = this.data["study"]["name"];
            let study_Max = this.data["study"]["Max"];
            let study_Exp = this.data["study"]["Exp"];
            let m_study = this.attrMore.getChildByName("study");
            let n_study = m_study.getChildByName("num");
            n_study.width = m_study.width * study_Exp / study_Max - 2;
            this.attrMore.getChildByName("study_STR2").getComponent(cc.Label).string = `${study_name} ${study_Exp}/${study_Max}`;
        } else {
            let m_study = this.attrMore.getChildByName("study");
            let n_study = m_study.getChildByName("num");
            n_study.width = 0;
            this.attrMore.getChildByName("study_STR2").getComponent(cc.Label).string = `未修炼武功`;
        }


    },
    getSkill() {
        this.skillbox.getChildren().forEach(item => {
            item.destroy();
        })
        this.data["skill"].forEach(item => {
            let box = cc.instantiate(this.skillperfab);
            let _pic = box.getChildByName("icon").getChildByName("img").getComponent(cc.Sprite);
            let _name = box.getChildByName("name").getComponent(cc.RichText);
            let _type = box.getChildByName("type").getComponent(cc.RichText);
            let _btn = box.getChildByName("btn");
            _name.string = `${item["name"]}  ${this.getSkillStatus(item["status"])}`;
            _pic.spriteFrame = com.loadFrame(`${RES_URL2}${item["icon"]}`);
            _type.string = `${this.getSkillType(item["type"])}`;
            if (~~item["type"] == 0 || ~~item["status"] == 1) _btn.active = false;
            else _btn.active = true;
            _btn.on(cc.Node.EventType.TOUCH_END, e => {
                let _tp = item["type"];
                this.data["skill"].forEach(obj => {
                    if (obj["type"] == _tp) obj.status = 0;
                });
                item["status"] = 1;
                this.getSkill();
            }, this);
            _btn.active = false;
            this.skillbox.addChild(box);
        })
    },
    getSkillStatus() {
        switch (~~arguments[0]) {
            case 1:
                return `<b><color=#fca500>运行中</color></b>`
                break;
            case 2:
                return `<b><color=#fca500>修炼中</color></b>`
                break;
            default:
                return "";
                break;
        }
    },
    getSkillType() {
        switch (~~arguments[0]) {
            case 0:
                return `<color=#fc2300>武</color>`
                break;
            case 1:
                return `<color=#0023fc>内</color>`
                break;
            case 2:
                return `<color=#23cc23>步</color>`
                break;
            default:
                return "";
                break;
        }
    },
    drawAttr() {
        let LER = Math.min(this.data["ability"]["LER"], 100);
        let DEF = Math.min(this.data["ability"]["DEF"], 100);
        let ATN = Math.min(this.data["ability"]["ATN"], 100);
        let SPD = Math.min(this.data["ability"]["SPD"], 100);
        let WIL = Math.min(this.data["ability"]["WIL"], 100);

        this.attrRich.getChildByName("LER").getComponent(cc.RichText).string = `<b>悟性<br/><color=#333333>${LER}</color></b>`;
        this.attrRich.getChildByName("DEF").getComponent(cc.RichText).string = `<b>硬功<br/><color=#333333>${DEF}</color></b>`;
        this.attrRich.getChildByName("ATN").getComponent(cc.RichText).string = `<b>气劲<br/><color=#333333>${ATN}</color></b>`;
        this.attrRich.getChildByName("SPD").getComponent(cc.RichText).string = `<b>轻功<br/><color=#333333>${SPD}</color></b>`;
        this.attrRich.getChildByName("WIL").getComponent(cc.RichText).string = `<b>内功<br/><color=#333333>${WIL}</color></b>`;

        this.polygon(this.graphics, 5, 75, 75, [100, 100, 100, 100, 100], Math.PI / 5);
        this.polygon(this.graphics2, 5, 75, 75, [LER, DEF, ATN, SPD, WIL], Math.PI / 5);
    },
    polygon(c, n, x, y, r, angle, counterclockwise) {
        angle = angle || 0;
        counterclockwise = counterclockwise || false;
        c.clear();
        let delta = 2 * Math.PI / n;
        c.moveTo(x + r[0] * Math.sin(angle), y - r[0] * Math.cos(angle));
        for (let i = 1; i < n; i++) {
            angle += counterclockwise ? -delta : delta;
            c.lineTo(x + ~~r[i] * Math.sin(angle), y - ~~r[i] * Math.cos(angle));
        }
        c.close();
        c.stroke();
        c.fill();
    }
    // update (dt) {},
})