const com = require("./common/common");
const parms = require("./parameter/setparms");
const school = require("./parameter/school");
const role = require("./parameter/role");
const RES_URL = "resources/img/role/head/";

cc.Class({
    extends: cc.Component,

    properties: {
        box0: {
            default: null,
            type: cc.Node
        },
        box1: {
            default: null,
            type: cc.Node
        },
    },

    onLoad() {
        this.role = {
            id: 0,
            name: "凌云",
            headpic: "face_00.png",
            ability: {
                Level: 1,
                MAXLevel: 99,
                EXP: 0,
                NEXT: 5,
                MAXHP: 52, //最大气血
                HP: 50, //气血
                MAXMP: 20, //最大内力
                MP: 20, //内力
                ATN: 35, //气劲
                DEF: 19, //硬功
                SPD: 28, //轻功
                WIL: 42, //内功
                LER: 54, //悟性
                WGT: 50, //负重        
                justice: 50 //正义
            },
            type: 0,
            equipment: [],
            skill: [],
            status: 0,
            study: null,
            sex: 1
        }
        this.chief = {
            id: 2,
            name: "都灵子",
            headpic: "face_20.png",
            ability: {
                Level: 1,
                MAXLevel: 70,
                EXP: 0,
                NEXT: 5,
                MAXHP: 50, //最大气血
                HP: 50, //气血
                MAXMP: 20, //最大内力
                MP: 20, //内力
                ATN: 5, //气劲
                DEF: 5, //硬功
                SPD: 5, //轻功
                WIL: 5, //内功
                LER: 5, //悟性
                WGT: 50, //负重        
                justice: 50 //正义
            },
            type: 0,
            equipment: [],
            skill: [],
            status: 0,
            study: null,
            sex: 1
        }

    },

    start() {
        this.showSceWord();
    },
    showSceWord() {
        this.map1 = this.node.getChildByName("map1");
        this.progress = 0;
        this.role_name = this.box0.getChildByName("name").getComponent(cc.Label);
        this.role_face = this.box0.getChildByName("face").getComponent(cc.Sprite);
        this.role_word = this.box0.getChildByName("word").getComponent(cc.RichText);
        this.chief_name = this.box1.getChildByName("name").getComponent(cc.Label);
        this.chief_face = this.box1.getChildByName("face").getComponent(cc.Sprite);
        this.chief_word = this.box1.getChildByName("word").getComponent(cc.RichText);
        this.goProgress();
        this.map1.on(cc.Node.EventType.TOUCH_END, e => {
            this.goProgress();
        })
    },
    getRoleBox(type, role, word) {
        if (type == 0) {
            this.role_name.string = this.role["name"];
            this.role_face.spriteFrame = com.loadFrame(`${RES_URL}${this.role["headpic"].split(".png")[0]}_200x200.png`);
            this.role_word.string = word;
            this.box1.active = false;
            this.box0.active = true;
        } else {
            this.chief_name.string = this.chief["name"];
            this.chief_face.spriteFrame = com.loadFrame(`${RES_URL}${this.chief["headpic"].split(".png")[0]}_200x200.png`);
            this.chief_word.string = word;
            this.box0.active = false;
            this.box1.active = true;
        }
    },
    goProgress() {
        switch (~~this.progress) {
            case 0:
                this.getRoleBox(0, this.role, `${parms.getSchool()}${this.role["name"]}拜见${this.chief["name"]}掌门。`);
                break;
            case 1:
                this.getRoleBox(1, this.chief, `哦，原来是${parms.getSchool()}的高手，不是前来何事啊？`);
                break;
            case 2:
                this.getRoleBox(0, this.role, `在下特此送来<color=#ff1c00>唐三彩骆驼</color>，奉上薄礼略表心意，望${this.chief["name"]}掌门笑纳。`);
                break;
            case 3:
                this.getRoleBox(1, this.chief, `哈哈哈哈，甚好甚好!`);
                break;
            default:
                cc.director.loadScene("sc_02")
                break;
        }
        this.progress++;
    }
    // update (dt) {},
});