const com = require("./common/common");
const parms = require("./parameter/setparms");
const school = require("./parameter/school");
const role = require("./parameter/role");

const RES_URL = "resources/img/role/head/";

cc.Class({
    extends: cc.Component,

    properties: {
        rolebox: {
            default: null,
            type: cc.Prefab
        },
        list: {
            default: null,
            type: cc.Node
        },
        school_name: {
            default: null,
            type: cc.Label
        },
        showbox: {
            default: null,
            type: cc.Node
        }
    },

    onLoad() {
        this.player = com.getplayer();
        this.school = school.getSchool(this.player);
        this.school_name.string = `${this.school["name"]}`;
        let arr = this.getMyRole();
        arr.forEach(item => {
            let rolebox = cc.instantiate(this.rolebox);
            let _head_pic = rolebox.getChildByName("head_pic").getComponent(cc.Sprite);
            let _name = rolebox.getChildByName("role_name").getChildByName("name").getComponent(cc.Label);
            let _status_a = rolebox.getChildByName("status_box").getChildByName("status_a").getComponent(cc.RichText);
            let _status_b = rolebox.getChildByName("status_box").getChildByName("status_b").getComponent(cc.Label);
            let _btn = rolebox.getChildByName("button");
            _name.string = item["name"];
            _status_a.string = this.ckStatusA(item["type"]);
            _status_b.string = this.ckStatusB(item["status"]);
            _head_pic.spriteFrame = com.loadFrame(`${RES_URL}${item["headpic"].split(".png")[0]}_200x200.png`);

            _btn.on(cc.Node.EventType.TOUCH_END, e => {
                this.showInfoBox(item);
            }, this);

            this.list.addChild(rolebox);
        })

    },

    start() {
        this.showbox.x = this.node.width;
        this.showbox.getChildByName("close").on(cc.Node.EventType.TOUCH_END, e => {
            this.showbox.runAction(cc.moveTo(.3, this.node.width, -35));
            this.school_name.string = `${this.school["name"]}`;
        }, this);
    },
    getMyRole() {
        let army = this.school.army;
        let arr = [];
        army.forEach(item => {
            arr.push(role.queryroleID(item));
        });
        return arr;
    },
    ckStatusA() {
        let type = arguments[0];
        let str = "状态：";
        switch (type) {
            case 0:
                str += '<color=#53fe82>健康</color>'
                break;
            case 1:
                str += '<color=#fe5382>受伤</color>'
                break;
            default:
                break;
        }
        return str;
    },
    ckStatusB() {
        let type = arguments[0];
        let str = "";
        switch (type) {
            case 0:
                str += '未执行任务'
                break;
            case 1:
                str += '已执行任务'
                break;
            default:
                break;
        }
        return str;
    },
    showInfoBox(item) {
        this.showbox.runAction(cc.moveTo(.3, 0, -35));
        this.school_name.string = `${item["name"]}`;
        this.showbox.emit("postInfo", {
                data: item
            })
            //console.log(item)
    }
    // update (dt) {},
});