const com = require("./common/common");
const system = require("./common/system");
const parms = require("./parameter/setparms");

cc.Class({
    extends: cc.Component,

    properties: {
        type: 0
    },


    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_END, e => {
            let obj = this.eventType(this.type)
            cc.director.loadScene(obj["scenes"]);
        }, this)
    },

    start() {

    },
    eventType() {
        switch (~~arguments[0]) {
            case 0:
                parms.removeEvent("shop_event")
                return {
                    scenes: "shop_system"
                }
                break;

            default:
                break;
        }
    }
    // update (dt) {},
});