    queryrole(name) {
        for (let i = 0; i < role.length; i++) {
            if (role[i]["name"] === name) return Object.assign(role[i]);
        }
    },
    getRoleData(name) {
        for (let i = 0; i < role.length; i++) {
            if (role[i]["name"] === name) return role[i];
        }
    },
    queryroleID(id) {
        for (let i = 0; i < role.length; i++) {
            if (role[i]["id"] === id) return Object.assign(role[i]);
        }
    },
