const com = require("./common/common");
const parms = require("./parameter/setparms");
const build = require("./parameter/build");
cc.Class({
    extends: cc.Component,
    properties: {
        build: {
            default: null,
            type: cc.Prefab
        },
        describe: {
            default: null,
            type: cc.Label
        },
        describe: {
            default: null,
            type: cc.Label
        },
        money: {
            default: null,
            type: cc.Label
        }
    },
    onLoad() {
        this.getMoney();
        this.describe.string = "";
        let list = this.node.getChildByName("list");
        let buildData = build.getBuildData();
        for (let i = 0; i < buildData.length; i++) {
            const element = buildData[i];
            let newBuild = cc.instantiate(this.build);
            let _box = newBuild.getChildByName("box");
            let _name = _box.getChildByName('name').getComponent(cc.Label);
            let _money = _box.getChildByName('money').getComponent(cc.Label);
            let _land = _box.getChildByName('land').getComponent(cc.Sprite);
            let _look = newBuild.getChildByName("look");
            let _add = newBuild.getChildByName("add");
            let _uplevel = newBuild.getChildByName("uplevel");
            _name.string = element.name;
            _land.spriteFrame = com.loadFrame("resources/img/build/" + element.src + ".png");
            let _obj = this.checkBuild(element.name);
            if (!_obj) _money.string = element.money[element.level] + com.getUnit();
            else {
                _add.active = false;
                _uplevel.active = true;
                if (_obj.maxlevel <= _obj.level) _money.string = "已至最大";
                else _money.string = _obj.money[_obj.level] + com.getUnit();
            }

            _look.on(cc.Node.EventType.TOUCH_END, e => {
                this.lookDescribe(element);
            }, this);
            _add.on(cc.Node.EventType.TOUCH_END, e => {
                if (this.checkMoney(element)) {
                    parms.setMyBuild(element.name);
                    _add.active = false;
                    _uplevel.active = true;
                    if (element.maxlevel <= element.level) _money.string = "已至最大";
                    else _money.string = element.money[element.level] + com.getUnit();
                    this.lookDescribe(element);
                    //this.node.dispatchEvent(new cc.Event.EventCustom('addBuild', true));
                }
            }, this);
            _uplevel.on(cc.Node.EventType.TOUCH_END, e => {
                if (element.maxlevel <= element.level) {
                    this.describe.string = "已至最大，无法扩建";
                    return;
                };
                if (this.checkMoney(element)) {
                    parms.upMyBuild(element.name);
                    if (element.maxlevel <= element.level) _money.string = "已至最大";
                    else _money.string = element.money[element.level] + com.getUnit();
                    this.lookDescribe(element);
                }
            }, this);

            list.addChild(newBuild);
        }
    },
    lookDescribe(element) {
        this.describe.string = `${element.name} (当前LV${element.level}) : ${element.describe}`;
    },
    getMoney() {
        this.money.string = parms.getAbility("Money");
    },
    checkMoney(element) {
        let myMoney = ~~parms.getAbility("Money");
        let n = ~~element.money[element.level];
        if (myMoney >= n) {
            parms.setAbility("Money", -n);
            this.getMoney();
            return true;
        } else {
            this.describe.string = "囊中羞涩";
            return false;
        }
    },
    checkBuild(name) {
        let myBuild = parms.getMyBuild();
        for (let i = 0; i < myBuild.length; i++) {
            if (myBuild[i]["name"] === name) return myBuild[i];
        }
    }
    // update (dt) {},
})