const com = require("./common/common");
const parms = require("./parameter/setparms");
const school = require("./parameter/school");
const role = require("./parameter/role");

const RES_URL = "resources/img/prop/";
const RES_URL_RH = "resources/img/role/head/";

cc.Class({
    extends: cc.Component,

    properties: {
        prop: {
            default: null,
            type: cc.Prefab
        },
        list: {
            default: null,
            type: cc.Node
        },
        showbox: {
            default: null,
            type: cc.Node
        },
        rolelist: {
            default: null,
            type: cc.Node
        },
        role: {
            default: null,
            type: cc.Prefab
        },
    },

    onLoad() {
        this.showbox.active = false;
        this.getData();
        this.getRoleList();
    },

    start() {

    },
    getData() {
        let all = parms.getPropAll();
        for (let i = 0; i < all.length; i++) {
            let item = all[i];
            let newProp = cc.instantiate(this.prop);
            let _pic = newProp.getChildByName("pic");
            let _name = newProp.getChildByName("name").getComponent(cc.Label);
            _name.string = item["name"];
            _pic.getComponent(cc.Sprite).spriteFrame = com.loadFrame(RES_URL + item["img"]);
            newProp.on(cc.Node.EventType.TOUCH_START, e => {
                _pic.scale = 1.25;
            }, this)
            newProp.on(cc.Node.EventType.TOUCH_END, e => {
                _pic.scale = 1;
                this._prop_obj = item;
                this.showData();
            }, this)
            this.list.addChild(newProp);
        }
    },
    showData() {
        let item = this._prop_obj;
        if (item) {
            this.showbox.active = true;
            let showbox = this.showbox;
            let pic = showbox.getChildByName("showimg").getChildByName("pic");
            let name = showbox.getChildByName("name").getComponent(cc.RichText);
            let describe = showbox.getChildByName("describe").getComponent(cc.RichText);
            let num = showbox.getChildByName("num").getComponent(cc.RichText);
            let type = showbox.getChildByName("type").getComponent(cc.RichText);
            let xiaoguo = showbox.getChildByName("xiaoguo").getComponent(cc.RichText);
            let owner = showbox.getChildByName("owner");
            pic.getComponent(cc.Sprite).spriteFrame = com.loadFrame(RES_URL + item["img"]);
            owner.active = false;
            name.string = `<b>${item["name"]}</b>`;
            describe.string = `${item["describe"]}`;
            num.string = `<b>数量：${item["num"]}</b>`;
            type.string = `${this.checkType(item["type"])}`;
            xiaoguo.string = this.getWord(item);
            this.changePropList()
        } else {
            this.showbox.active = false;
        }
    },
    getRoleList() {
        this.player = com.getplayer();
        this.school = school.getSchool(this.player);
        let arr = this.getMyRole();
        this.roleData = arr;
        for (let i = 0; i < arr.length; i++) {
            let item = arr[i];
            let newBox = cc.instantiate(this.role);
            let _pic = newBox.getChildByName("pic");
            let _name = newBox.getChildByName("name").getComponent(cc.Label);
            let _use = newBox.getChildByName("use");
            let _up = newBox.getChildByName("up");
            let _learn = newBox.getChildByName("learn");
            let _cp_learn = newBox.getChildByName("cp-learn");
            _use.active = false;
            _up.active = false;
            _learn.active = false;
            _cp_learn.active = false
            _pic.getComponent(cc.Sprite).spriteFrame = com.loadFrame(`${RES_URL_RH}${item["headpic"].split(".png")[0]}_200x200.png`);
            _name.string = `${item["name"]}`;
            //使用药物
            _use.on(cc.Node.EventType.TOUCH_END, e => {
                if (this._prop_obj["num"] > 0) {
                    this._prop_obj["num"]--;
                    this.closePropItem();
                    let attrArr = this.getProperty(this._prop_obj);
                    attrArr.forEach(n => {
                        parms.setAbility(n, this._prop_obj[n], item);
                    })
                }
            }, this);
            //学习秘籍
            _learn.on(cc.Node.EventType.TOUCH_END, e => {
                if (!this.checkSkill(item, this._prop_obj["skill"])) {
                    if (this._prop_obj["num"] > 0) {
                        this._prop_obj["num"]--;
                        this.closePropItem();
                        parms.setSkill(this._prop_obj["skill"], item);
                        let attrArr = this.getProperty(this._prop_obj);
                        attrArr.forEach(n => {
                            parms.setAbility(n, this._prop_obj[n], item);
                        })
                    }
                }
            }, this);
            //添加装备
            _up.on(cc.Node.EventType.TOUCH_END, e => {
                parms.setEquipment(this._prop_obj, item);
                let attrArr = this.getProperty(this._prop_obj);
                attrArr.forEach(n => {
                    parms.setAbility(n, this._prop_obj[n], item);
                })
                console.log(this.getMyRole());
            }, this);
            this.rolelist.addChild(newBox);

        }
    },
    checkSkill(obj, name) {
        let bool = false;
        for (let i = 0; i < obj["skill"].length; i++) {
            const item = obj["skill"][i];
            if (item["name"] == name) {
                bool = true;
            }
        }
        return bool;
    },
    getMyRole() {
        let army = this.school.army;
        let arr = [];
        army.forEach(item => {
            arr.push(role.queryroleID(item));
        });
        return arr;
    },
    checkType() {
        switch (~~arguments[0]) {
            case 0:
                return '材料';
                break;
            case 1:
                return '补药';
                break;
            case 2:
                return '宝物';
                break;
            case 3:
                return '药物';
                break;
            case 4:
                return '武具';
                break;
            case 5:
                return '秘籍';
                break;
            case 6:
                return '防具';
                break;
            case 7:
                return '奇物';
                break;
            default:
                break;
        }
    },
    getProperty(item) {
        let arr = [];
        let attr = ["MAXHP", "MAXMP", "LER", "DEF", "ATN", "SPD", "WIL"];
        switch (item["type"]) {
            case 1:
                for (const key in item) {
                    if (item.hasOwnProperty(key)) {
                        if (attr.indexOf(key) > -1) {
                            arr.push(key)
                        }
                    }
                }
                break;
            case 3:
                if (item.hasOwnProperty("HP")) {
                    arr.push("HP");
                }
                if (item.hasOwnProperty("MP")) {
                    arr.push("MP");
                }
                break;
            case 4:
                break;
            case 5:
                for (const key in item) {
                    if (item.hasOwnProperty(key)) {
                        if (attr.indexOf(key) > -1) {
                            arr.push(key)
                        }
                    }
                }
                break;
            case 6:
                break;
            case 7:
                for (const key in item) {
                    if (item.hasOwnProperty(key)) {
                        if (attr.indexOf(key) > -1) {
                            arr.push(key)
                        }
                    }
                }
                break;
                break;
            default:
                break;

        }

        return arr;

    },
    getWord(item) {
        let str = "";
        let size = 16;
        if (item.hasOwnProperty("special")) {
            str += `<size=${size}><color=#FF19ee>${item["special"]}</color></size><br/>`
        }
        if (item.hasOwnProperty("skill")) {
            str += `<size=${size}>可以学习<color=#FFa955>${item["skill"]}</color></size><br/>`
        }
        if (item.hasOwnProperty("power")) {
            str += `<size=${size}>可以增加<color=#FF3434>${item["power"]}</color>点伤害</size><br/>`
        }
        if (item.hasOwnProperty("MAXMP")) {
            str += `<size=${size}>可以提高<color=#FFa955>${item["MAXMP"]}</color>点真气上限</size><br/>`
        }
        if (item.hasOwnProperty("MAXHP")) {
            str += `<size=${size}>可以提高<color=#FFa955>${item["MAXHP"]}</color>点气血上限</size><br/>`
        }
        if (item.hasOwnProperty("diplomacy")) {
            str += `<size=${size}>可以增加<color=#FFa955>${item["diplomacy"]}</color>点友好度</size><br/>`
        }
        if (item.hasOwnProperty("offset")) {
            str += `<size=${size}>可以抵挡<color=#d449ff>${item["offset"]}</color>点伤害</size><br/>`
        }
        if (item.hasOwnProperty("HP")) {
            str += `<size=${size}>可以恢复<color=#33aa66>${item["HP"]}</color>点气血</size><br/>`
        }
        if (item.hasOwnProperty("MP")) {
            str += `<size=${size}>可以恢复<color=#33aa66>${item["MP"]}</color>点真气</size><br/>`
        }
        if (item.hasOwnProperty("LER")) {
            str += `<size=${size}>可以提升<color=#008B45>${item["LER"]}</color>点悟性</size><br/>`
        }
        if (item.hasOwnProperty("DEF")) {
            str += `<size=${size}>可以提升<color=#008B45>${item["DEF"]}</color>点硬功</size><br/>`
        }
        if (item.hasOwnProperty("ATN")) {
            str += `<size=${size}>可以提升<color=#008B45>${item["ATN"]}</color>点气劲</size><br/>`
        }
        if (item.hasOwnProperty("SPD")) {
            str += `<size=${size}>可以提升<color=#008B45>${item["SPD"]}</color>点轻功</size><br/>`
        }
        if (item.hasOwnProperty("WIL")) {
            str += `<size=${size}>可以提升<color=#008B45>${item["WIL"]}</color>点内功</size><br/>`
        }
        return str;
    },
    changePropList() {
        let list = this.rolelist.getChildren();
        let item = this._prop_obj;
        for (let i = 0; i < list.length; i++) {
            let elem = list[i];
            let _name = elem.getChildByName("name").getComponent(cc.Label);
            let _attr = elem.getChildByName("attr").getComponent(cc.RichText);
            let _use = elem.getChildByName("use");
            let _up = elem.getChildByName("up");
            let _learn = elem.getChildByName("learn");
            let _cp_learn = elem.getChildByName("cp-learn");
            _attr.string = "";
            _cp_learn.active = false;
            if (item["type"] == 0 || item["type"] == 2) {
                _use.active = false;
                _up.active = false;
                _learn.active = false;
            } else if (item["type"] == 1 || item["type"] == 3) {
                if (item) {
                    let attrArr = this.getProperty(this._prop_obj);
                    attrArr.forEach(n => {
                        const aly = role.getRoleData(_name.string)["ability"];
                        const now = aly[n];
                        let after = 0;
                        let sum = ~~now + this._prop_obj[n];
                        if (n == "HP") {
                            after = (sum > ~~aly["MAXHP"]) ? aly["MAXHP"] : sum;
                        } else if (n == "MP") {
                            after = (sum > ~~aly["MAXMP"]) ? aly["MAXMP"] : sum;
                        } else {
                            after = ~~now + this._prop_obj[n];
                        }
                        if (now >= after) {
                            _attr.string = `<color=#23cc00>${now} → ${after}</color><br/>`;
                        } else {
                            _attr.string = `<color=#cc2300>${now} → ${after}</color><br/>`;
                        }
                    })
                }
                _use.active = true;
                _up.active = false;
                _learn.active = false;
            } else if (item["type"] == 4 || item["type"] == 6 || item["type"] == 7) {
                _use.active = false;
                _up.active = true;
                _learn.active = false;
            } else if (item["type"] == 5) {
                if (!this.checkSkill(role.getRoleData(_name.string), item["skill"])) {
                    _learn.active = true;
                    _cp_learn.active = false;
                } else {
                    _cp_learn.active = true;
                    _learn.active = false;
                }
                _use.active = false;
                _up.active = false;
            }
        }
    },
    closePropItem() {
        let list = this.list.getChildren();
        let item = this._prop_obj;
        if (item["num"] == 0) {
            for (let i = 0; i < list.length; i++) {
                let elem = list[i];
                let name = elem.getChildByName("name").getComponent(cc.Label);
                if (name.string == item["name"]) {
                    elem.active = false;
                }
            }
            parms.removeProp(item);
        }
    },
    update(dt) {
        let item = this._prop_obj;
        if (item) {
            let num = this.showbox.getChildByName("num").getComponent(cc.RichText);
            let owner = this.showbox.getChildByName("owner");
            if (item["type"] == 4 || item["type"] == 6 || item["type"] == 7) {
                owner.active = true;
                let or = item["owner"] || "无";
                owner.getComponent(cc.RichText).string = `拥有：${or}`
            }
            num.string = `<b>数量：${ item["num"]}</b>`;
            this.changePropList();
        }
    },
});