const parms = require("setparms");
const skill = require("skill");
const prop = require("prop");
const build = require("build");
const school = require("school");

// Money: 0,
// HP: 50, //体力
// MP: 20, //内力
// ATN: 5, //气劲
// DEF: 5, //硬功
// SPD: 5, //轻功
// WIL: 5, //内功
// LER: 5, //悟性
// WGT: 50, //负重
// repute: 5, //声望
// justice: 50 //正义


let api = {
    Opening() {
        let option = parms.getOptionAll(5);
        //问题1：背景
        switch (~~option[0].id) {
            case 0:
                parms.setAbility("HP", 5);
                parms.setAbility("MP", 5);
                parms.setAbility("MAXHP", 5);
                parms.setAbility("MAXMP", 5);
                parms.setAbility("ATN", 1);
                parms.setAbility("DEF", 1);
                break;
            case 1:
                parms.setAbility("HP", 10);
                parms.setAbility("MAXHP", 10);
                parms.setAbility("SPD", 1);
                parms.setAbility("WGT", 5);
                break;
            case 2:
                parms.setAbility("MP", 5);
                parms.setAbility("MAXMP", 5);
                parms.setAbility("LER", 1);
                parms.setAbility("WGT", -2);
                break;
            case 3:
                parms.setAbility("HP", 10);
                parms.setAbility("MP", 5);
                parms.setAbility("MAXHP", 10);
                parms.setAbility("MAXMP", 5);
                parms.setAbility("WGT", 10);
                break;
            default:
                break;
        }
        //问题2：武功
        let newskill = null;
        switch (~~option[1].id) {
            case 0:
                newskill = skill.querySkill("太祖长拳");
                break;
            case 1:
                newskill = skill.querySkill("御剑术");
                break;
            case 2:
                newskill = skill.querySkill("柴刀十八路");
                break;
            case 3:
                newskill = skill.querySkill("太祖棍法");
                break;
            default:
                break;
        }
        parms.setSkill(newskill);

        //问题3：强项
        switch (~~option[2].id) {
            case 0:
                parms.setAbility("ATN", 1);
                break;
            case 1:
                parms.setAbility("DEF", 1);
                break;
            case 2:
                parms.setAbility("SPD", 1);
                break;
            case 3:
                parms.setAbility("WIL", 1);
                break;
            default:
                break;
        }

        //问题4：心性
        switch (~~option[3].id) {
            case 0:
                parms.setAbility("HP", 10);
                parms.setAbility("MAXHP", 10);
                parms.setAbility("justice", 5);
                break;
            case 1:
                parms.setAbility("LER", 1);
                parms.setAbility("justice", 5);
                break;
            case 2:
                parms.setAbility("repute", -5)
                parms.setAbility("SPD", 1);
                break;
            case 3:
                parms.setAbility("justice", -20);
                parms.setAbility("WIL", 1);
                break;
            default:
                break;
        }

        //问题5：发展
        switch (~~option[4].id) {
            case 0:
                parms.setAbility("Money", 2000);
                break;
            case 1:
                var _prop = {
                    "img": "miji0.png",
                    "name": "吐纳秘典",
                    "price": 2400,
                    "describe": "记录了吐纳心法修炼法门。",
                    "skill": "吐纳心法",
                    "type": 5,
                    "MAXHP": "12",
                    "MAXMP": "18",
                    "nosale": false,
                    "num": 1
                }
                parms.setProp(_prop);
                break;
            case 2:
                var _prop = {
                    "id": -1,
                    "img": "xinyuanyima.png",
                    "name": "心猿意马",
                    "price": 1,
                    "describe": "一只有灵性的乌龟。",
                    "special": "或许在一些事上能带来好运",
                    "MAXHP": 25,
                    "LER": 2,
                    "type": 7,
                    "nosale": true
                }
                parms.setProp(_prop);
                break;
            case 3:
                parms.setAbility("repute", 20);
                break;
            default:
                break;
        }
    }
}
module.exports = api;