let school = [
    { id: 0, name: "派", army: [0, 1], msg: ['伽耶正在烹制着美味的三文鱼...', '伽耶正在辛苦的健身...'] },
    { id: 1, name: "蓬莱派", army: [2], msg: ['温仁厚仰望天空若有所思...'] },
    { id: 2, name: "泰山派", army: [3], msg: ['玉磐子好像偷偷下山去了...'] },
    { id: 3, name: "黑风寨", army: [4], msg: ['韩青风给山寨众山贼洗脑中...', '周文对着镜子坏笑中...'] },
]

module.exports = {
    getSchool() {
        let index = arguments[0] + "";
        if (!index) return school;
        return school[~~index];
    },
    get() {
        return school;
    },
    querySchool(name) {
        for (let i = 0; i < school.length; i++) {
            if (school[i]["name"] === name) return Object.assign(school[i]);
        }
    }
}