const com = require("./common/common");
const parms = require("./parameter/setparms");

const RES_URL = "resources/img/prop/";

cc.Class({
    extends: cc.Component,

    properties: {
        money: {
            default: null,
            type: cc.Label
        },
        goods: {
            default: null,
            type: cc.Prefab
        },
        box: {
            default: null,
            type: cc.Node
        },
        word: {
            default: null,
            type: cc.RichText
        },
    },

    onLoad() {
        this.getMoney();
        cc.loader.loadRes("data/prop", cc.RawAsset, (err, res) => {
            this.goodsData = [];
            parms.setPropData(res.data);
            res.data.forEach(item => {
                if (!item["nosale"]) this.goodsData.push(item);
            })
            this.InitData();
        })
    },

    start() {

    },
    InitData() {
        let goodsData = this.goodsData;
        let goodsArr = [];
        let len = goodsData.length;
        let goodsNum = 4 + ~~(Math.random() * 3);

        if (goodsData.length > goodsNum) {
            while (goodsArr.length < goodsNum) {
                let index = ~~(Math.random() * len)
                let obj = goodsData[index];
                let bool = false;
                for (let i = 0; i < goodsArr.length; i++) {
                    const element = goodsArr[i];
                    if (element["name"] == obj["name"]) {
                        bool = true;
                        break;
                    }
                }
                if (!bool) goodsArr.push(obj);
            }
        }
        goodsArr.forEach((item, index) => {
            let newGoods = cc.instantiate(this.goods);
            let _pic = newGoods.getChildByName("box").getChildByName("pic").getComponent(cc.Sprite);
            let _name = newGoods.getChildByName("name").getComponent(cc.RichText);
            let _price = newGoods.getChildByName("mbox").getChildByName("price").getComponent(cc.RichText);
            let buy_btn = newGoods.getChildByName("buybtn");
            let look_btn = newGoods.getChildByName("lookbtn");
            _pic.spriteFrame = com.loadFrame(RES_URL + item["img"]);
            _name.string = item["name"];
            _price.string = item["price"].toString();
            look_btn.on(cc.Node.EventType.TOUCH_END, e => {
                this.word.string = this.getWord(item);
            }, this)
            buy_btn.on(cc.Node.EventType.TOUCH_END, e => {
                if (~~parms.getAbility("Money") < item["price"]) {
                    let arr = ["小伙子，你这钱好像不太够啊！？", "穷光蛋走走走！别耽误我做生意。", "您这不拿我寻开心不是！？就这点钱根本买不着！", `看清楚啦！这个可是需要<color=#FFd900>${item["price"]}两</color>，要不你再看看别的？`];
                    this.word.string = arr[~~(Math.random() * arr.length)];
                } else {
                    parms.setAbility("Money", -item["price"]);
                    let arr = ["您收好啦，你这小伙子眼光可真好。", `您真识货这<color=#FFd900>${item["name"]}</color>可算买着了。`]
                    this.word.string = arr[~~(Math.random() * arr.length)];
                    this.getMoney();
                    buy_btn.getComponent(cc.Button).interactable = false;
                    buy_btn.opacity = 180;
                    buy_btn.off(cc.Node.EventType.TOUCH_END);
                    parms.setProp(item); //添加到玩家仓库
                }
            }, this)
            this.box.addChild(newGoods);
        })
    },
    getWord(item) {
        let str = `${item["describe"]}<br/>`;
        let size = 14;
        if (item.hasOwnProperty("special")) {
            str += `<size=${size}><color=#FFd900>${item["special"]}</color></size><br/>`
        }
        if (item.hasOwnProperty("skill")) {
            str += `<size=${size}>可以学习<color=#FFd900>${item["skill"]}</color></size><br/>`
        }
        if (item.hasOwnProperty("power")) {
            str += `<size=${size}>可以增加<color=#FF3434>${item["power"]}</color>点伤害</size><br/>`
        }
        if (item.hasOwnProperty("MAXMP")) {
            str += `<size=${size}>可以提高<color=#FFd900>${item["MAXMP"]}</color>点真气上限</size><br/>`
        }
        if (item.hasOwnProperty("MAXHP")) {
            str += `<size=${size}>可以提高<color=#FFd900>${item["MAXHP"]}</color>点气血上限</size><br/>`
        }
        if (item.hasOwnProperty("diplomacy")) {
            str += `<size=${size}>可以增加<color=#FFd900>${item["diplomacy"]}</color>点友好度</size><br/>`
        }
        if (item.hasOwnProperty("offset")) {
            str += `<size=${size}>可以抵挡<color=#34d9ff>${item["offset"]}</color>点伤害</size><br/>`
        }
        if (item.hasOwnProperty("HP")) {
            str += `<size=${size}>可以恢复<color=#33FF66>${item["HP"]}</color>点气血</size><br/>`
        }
        if (item.hasOwnProperty("MP")) {
            str += `<size=${size}>可以恢复<color=#33FF66>${item["MP"]}</color>点真气</size><br/>`
        }

        if (item.hasOwnProperty("LER")) {
            str += `<size=${size}>可以提升<color=#008B45>${item["LER"]}</color>点悟性</size><br/>`
        }
        if (item.hasOwnProperty("DEF")) {
            str += `<size=${size}>可以提升<color=#008B45>${item["DEF"]}</color>点硬功</size><br/>`
        }
        if (item.hasOwnProperty("ATN")) {
            str += `<size=${size}>可以提升<color=#008B45>${item["ATN"]}</color>点气劲</size><br/>`
        }
        if (item.hasOwnProperty("SPD")) {
            str += `<size=${size}>可以提升<color=#008B45>${item["SPD"]}</color>点轻功</size><br/>`
        }
        if (item.hasOwnProperty("WIL")) {
            str += `<size=${size}>可以提升<color=#008B45>${item["WIL"]}</color>点内功</size><br/>`
        }
        return str;
    },
    getMoney() {
        this.money.string = parms.getAbility("Money");
    },
    // update (dt) {},
});